    display: flex;
    flex-direction: row;
    justify-content: space-between;
    grid-area: header;
    align-items: center;
    background-color: white;
    box-shadow: 0px 2px 10px rgba(109, 109, 109, 0.25), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
    padding: 15px;
    height: 30px;
    margin-bottom: 0;
}

.headerFlex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
}


.imgWrapper {
    width: 50px;
    height: 50px;
}

.header img {
    max-height: 100%;
}

.titleWrapper {

}

.titleWrapper p {
    font-weight: bold;
}

