.header img {
    width: 50px;
}

.loginBlock {
    margin: 10px;
    float: right;
}

.loginBlock a {
    color: black;

}

.name {
    margin: 15px 5px;
    color: black;
    font-weight: bold;
}

.logo {
    width: 40px;
    margin: 5px 5px;

}

.logoAndTitle {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.loginBlock {
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginBlock > span {
    margin-right: 10px;
}

.link {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    font-weight: bold;
}

.link:visited {
    color: black;
}

.link:link {
    color: black;
}

.active {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: bold;
    text-decoration: none;
    color: black;
}